import * as React from 'react';
import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import Avatar from '@mui/material/Avatar';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';



function Review(props) {
    const { reviews } = props;

    return (
      <Card item md={6} sx={{ margin: "10px", padding: "10px", width: "50%", minWidth: "300px", backgroundColor: "#f5f5f5", display: "flex", flexDirection: "column" }} >
        <Box sx={{ display: "flex", flexDirection: "row", alignItems: "center"}}>
          <Avatar
            sx={{ height: "75px", width: "75px", margin: "20px 20px", boxShadow: "0px 2px 0px #aaa" }}
            src={reviews.photo}
            alt="Fictional person"
            >
          </Avatar>
          <Box>
            <Typography sx={{ fontSize: "1.2em", textAlign: "left", fontWeight: "bold" }} color="text.primary">
                {reviews.name}
            </Typography>
            <Typography sx={{ fontSize: "1em", textAlign: "left", fontStyle: "italic" }} color="text.secondary">
            {reviews.company}
            </Typography>
          </Box>
        </Box>

        <CardContent sx={{ alignSelf: "end", width: "100%"}}>
          <Typography variant="body1">
            {reviews.comment}
          </Typography>
        </CardContent>
      </Card>
    );
  }

  Review.propTypes = {
    post: PropTypes.shape({
      name: PropTypes.string.isRequired,
      company: PropTypes.string.isRequired,
      photo: PropTypes.string.isRequired,
      comment: PropTypes.string.isRequired,
    }).isRequired,
  };

export default Review;