import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  boxShadow: 24,
  p: 4,
};

export default function BasicModal() {
  const [open, setOpen] = React.useState(true);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            This is mock-up website!
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            All information available in this website in fake and made-up to try out developing in React.
            The book is not written (yet) and not available to buy.
            In Reviews section listed people do not exist, their names are made up and photos are <a href="https://thispersondoesnotexist.com/">AI generated</a>.
            In About the Author section the photo is not me. It was taken from <a href="https://images.pexels.com/photos/5951365/pexels-photo-5951365.jpeg">Pexels</a>. 
            For the Audiobook ilustration the photo from <a href="https://www.freepik.com/free-psd/arrangement-music-elements-blue-background-with-phone-mock-up_10301193.htm">Freepik</a> is used.
          </Typography>
        </Box>
      </Modal>
    </div>
  );
}
